@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    local("Raleway Regular"),
    local("Raleway-Regular"),
    url("../fonts/raleway-400.woff2") format("woff2"),
    url("../fonts/raleway-400.woff") format("woff");
}

